angular.module('sp-app').factory('Cloud', function() {
    function Cloud(name) {
        this.name = name;
        this.tracks = [];
        this.user = "";
    }
    Cloud.prototype.addTrack = function(track) {
    	track.cloud  = this;
    	this.tracks.push(track);
    }
    Cloud.prototype.removeTrack = function(track) {
    	track.cloud = null;
    	_.remove(this.tracks, track);
    }
    return Cloud;
});