angular.module('sp-app').factory('Album', function() {
    function Album(name) {
        this.name = name;
        this.tracks = [];
        this.albumArtPath = "";
    }
    Album.prototype.addTrack = function(track) {
    	track.album  = this;
    	this.tracks.push(track);
    }
    Album.prototype.removeTrack = function(track) {
    	track.album = null;
    	_.remove(this.tracks, track);
    }
    return Album;
});
