angular.module('sp-app').factory('loader', function() {
    
    var loader = null;
    return {
        show: function(options) {
          

           loader = document.querySelector('.library_loader');
           loader.style.display = 'flex';

        },
        hide: function() {
           if(!loader) {
                loader =  document.querySelector('.library_loader')
           }
           loader.style.display = 'none'; 
        }   
    }
})